import React from 'react';
import { observer } from 'mobx-react';
import { useTranslation } from 'react-i18next';
import { changeTitle } from '../routes';
import { QuickstartConfig } from './Partials/QuickstartConfig';
import { Card, Flex, PageHeader } from '@/components';
import css from './Applications.module.scss'
import cx from 'classnames';

const AppsQuickstart = observer(() => {
  const {t} = useTranslation();
  
  changeTitle(t('routes.quickstart'));

  return (
    <div className={css.quickstartWrapper}>
      <Flex justifyContent={'space-between'} alignItems='center'>
        <PageHeader title={t('routes.quickstart')} />
      </Flex>

      <Card className={cx(css.card, css.cardContent, css.cardSpacer)}>
        <QuickstartConfig standalone={false} />
      </Card>
    </div>
  )
})

export default AppsQuickstart